body {
    /*background-image: linear-gradient(to bottom left, #c8d8e4, white);*/
    background-image: url("https://img.freepik.com/free-vector/white-color-background-gradient-abstract-modern_343694-2130.jpg?t=st=1727206708~exp=1727210308~hmac=b2fd93baf41cfb52e6bf78d568e9f4db5826a4aa694aec7bbece3892b0133bad&w=2000");
    font-family: "Helvetica";
    color: #333
}

html,
body,
#root {
    height: 100%;
    margin: 0px;
}

.component {
    box-sizing: border-box;
    background-color: #fff;
    width: 100%;
    max-width: 1000px;
    padding: 20px;
    margin: 0px auto 40px auto;
    border-radius: 5px;
    box-shadow: 10px 5px 5px #c8d8e4;
}

.default-layout {
    height: 100%;
    /*background: #F4F4F9;*/
    display: flex;
    flex-direction: column;
}

.header-navbar {
    display: flex;
    justify-content: center;
    background-color: seagreen;
    padding: 10px;
    margin-bottom: 40px;
}

/*.header-navbar:hover {*/
/*    cursor: pointer;*/
/*}*/

.header-navbar-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 1000px;
}

.header-navbar-left,
.header-navbar-right {
    display: flex;
}

.nav-icon {
    padding-right: 20px;
    font-size: 20px;
    color: #fff;
}

.nav-icon:hover {
    cursor: pointer;
}

.nav-link-icon:hover {
    cursor: pointer;
}

.nav-icon:last-child {
    padding-right: 0px;
}

.logo-icon {
    font-size: 40px;
    color: #fff;
}

.page-navbar {
    font-size: 18px;
    display: flex;
    width: 100%;
    max-width: 1000px;
    margin: 0 auto;
}

.account-selector {
    display: flex;
    justify-content: center;
    font-weight: bold;
    align-items: center;
}

.nav-link {
    display: flex;
    padding: 15px 25px;
    border-radius: 4px 4px 0 0;
    text-decoration: none;
    color: #333;
    transition: background-color 0.5s ease;
}

.nav-link:hover {
    background-color: #EBEDEF;
}

.nav-link.active {
    background-color: #fff;
}

.nav-link-icon {
    margin-right: 10px
}

.account-name {
    margin-right: 10px;
    border: solid 1px;
    border-radius: 14px;
    padding: 20px;
    color: #333;
    background-color: white;
}

.account-number {
    font-size: x-large;
}

.account-name-details {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 80px;
    font-size: larger;
}

.bank-name {
    padding-top: 10px;
    padding-left: 20px;
    color: white;
    font-size: x-large;
}

.user-name {
    padding-right: 20px;
    font-size: larger;
    color: white;
}

.app-btn {
    color: white;
    background-color: seagreen;
    height: 44px;
    border: none;
}

.app-btn:hover {
    cursor: pointer;
}

.form-layout {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    /*background: #f6f8fa;*/
    background-image: url("https://img.freepik.com/free-vector/white-color-background-gradient-abstract-modern_343694-2130.jpg?t=st=1727206708~exp=1727210308~hmac=b2fd93baf41cfb52e6bf78d568e9f4db5826a4aa694aec7bbece3892b0133bad&w=2000");
}

.form-component {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    background-color: #fff;
    max-width: 800px;
    margin: 0px auto 40px auto;
    border-radius: 5px;
}

.form-banner {
    display: flex;
    background-color: seagreen;
    padding: 30px;
    align-items: center;
    justify-content: center;
    border-radius: 5px 5px 0 0;
}

.banner-icon {
    font-size: 55px;
    color: #fff;
}

.form-container {
    padding: 30px;
    border-radius: 0 0 5px 5px;
}

.home {}

.home-header {
    display: flex;
    justify-content: center;
    padding: 20px;
    font-size: 33px;
    color: #16A085;
}

.account {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    border-bottom: 1px solid #d0d3d4;
    font-size: 18px;
}

.account div {
    flex: 1 1;
    text-align: center;
}

.account div:first-child {
    flex: 2 1;
    text-align: left;
}

.account:nth-child(even) {
    background-color: #e3ecec;
}

.account:hover {
    cursor: pointer;
}
.transaction {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    border-bottom: 1px solid #d0d3d4;
    font-size: 18px;
}

.transaction:nth-child(even) {
    background-color: #e3ecec;
}

