.transaction-form .form-row {
    display: flex;
    width: 50%;
    align-items: center;
    margin-bottom: 20px;
}

.transaction-form .form-row label {
    display: inline-block;
    font-size: 15px;
    width: 90px;
    text-align: right;
    margin-right: 10px;
}

.transaction-form .form-row input {
    height: 25px;
    min-width: 200px;
    width: 100%;
}

.transaction-form .form-row.submit-btn {
    justify-content: end;
}
